import React, { createContext, useContext, useState, useEffect } from 'react';

const ServerConfigContext = createContext();

export const useServerConfig = () => {
  const context = useContext(ServerConfigContext);
  if (!context) {
    throw new Error('useServerConfig must be used within a ServerConfigProvider');
  }
  return context;
};

export const ServerConfigProvider = ({ children }) => {
  const [serverUrl, setServerUrl] = useState(() => {
    return localStorage.getItem('codeMapsServerUrl') || 'http://localhost:3001';
  });
  
  const [serverStatus, setServerStatus] = useState('checking');
  const [isChecking, setIsChecking] = useState(false);

  const checkServerConnection = async (url = serverUrl, callback = null) => {
    setIsChecking(true);
    setServerStatus('checking');
    
    // Create an AbortController for timeout
    const controller = new AbortController();
    const timeoutId = setTimeout(() => controller.abort(), 5000);
    
    try {
      // First, check the health endpoint
      const healthResponse = await fetch(`${url}/api/health`, {
        method: 'GET',
        signal: controller.signal
      });
      
      if (!healthResponse.ok) {
        clearTimeout(timeoutId);
        const status = 'disconnected';
        setServerStatus(status);
        if (callback) callback(status);
        return;
      }
      
      const healthData = await healthResponse.json();
      if (healthData.status !== 'ok') {
        clearTimeout(timeoutId);
        const status = 'disconnected';
        setServerStatus(status);
        if (callback) callback(status);
        return;
      }
      
      // Then, test a basic API functionality (browse root directory)
      const browseResponse = await fetch(`${url}/api/browse?path=~`, {
        method: 'GET',
        signal: controller.signal
      });
      
      clearTimeout(timeoutId);
      
      if (browseResponse.ok) {
        const status = 'connected';
        setServerStatus(status);
        if (callback) callback(status);
      } else {
        const status = 'disconnected';
        setServerStatus(status);
        if (callback) callback(status);
      }
    } catch (error) {
      clearTimeout(timeoutId);
      console.log('Server connection failed:', error.message);
      const status = 'disconnected';
      setServerStatus(status);
      if (callback) callback(status);
    } finally {
      setIsChecking(false);
    }
  };

  const updateServerUrl = (newUrl) => {
    setServerUrl(newUrl);
    localStorage.setItem('codeMapsServerUrl', newUrl);
    checkServerConnection(newUrl);
  };

  // Check connection on mount and when serverUrl changes
  useEffect(() => {
    checkServerConnection();
  }, []);

  const value = {
    serverUrl,
    serverStatus,
    isChecking,
    updateServerUrl,
    checkServerConnection
  };

  return (
    <ServerConfigContext.Provider value={value}>
      {children}
    </ServerConfigContext.Provider>
  );
}; 