import React, { memo } from 'react';
import { useColorMode } from '../contexts/ColorModeContext';

export default memo(({ data, children }) => {
  const { accessModifier, returnType, arguments: methodArgs, classColor } = data;
  const { colorMode } = useColorMode();
  // Create method signature display
  const argsString = methodArgs && methodArgs.length > 0 
    ? methodArgs.map(arg => `${arg.type} ${arg.name}`).join(', ')
    : 'no args';
  
  // Get the appropriate color based on access modifier
  const labelColor = accessModifier === 'public' ? classColor?.light : classColor?.dark;
  
  return (
    <div>
      {/* Method name label positioned at top left corner */}
      <div style={{
        position: 'absolute',
        top: '-22px',
        left: '2px',
        backgroundColor: labelColor,
        color: classColor?.contrastText,
        padding: '4px 8px',
        borderRadius: '4px',
        borderBottomLeftRadius: '0px',
        borderBottomRightRadius: '0px',
        fontSize: '12px',
        fontWeight: 'bold',
        zIndex: 10,
      }}>
        {data.label}
      </div>
      
      {/* Method signature details */}
      <div style={{
        position: 'absolute',
        top: '-35px',
        left: '0px',
        color: colorMode === 'dark' ? 'white' : 'black',
        padding: '2px 4px',
        borderRadius: '2px',
        fontSize: '8px',
        zIndex: 9,
        maxWidth: '180px',
        overflow: 'hidden',
        textOverflow: 'ellipsis',
        whiteSpace: 'nowrap'
      }}>
        ({argsString})  {returnType || 'void'}
      </div>
      
      {/* Group content */}
      {children}
    </div>
  );
}); 