import React, { useState, useEffect } from 'react';
import { useServerConfig } from '../contexts/ServerConfigContext';

const FileSystemBrowser = ({ onFilesSelected, selectedFiles: externalSelectedFiles, onGenerateGraph, onOpenServerSettings }) => {
  const { serverUrl, serverStatus } = useServerConfig();
  const [currentPath, setCurrentPath] = useState('~');
  const [directoryContents, setDirectoryContents] = useState(null);
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState('');
  const [selectedFiles, setSelectedFiles] = useState(externalSelectedFiles || []);

  // Sync with external selectedFiles prop
  useEffect(() => {
    if (externalSelectedFiles) {
      setSelectedFiles(externalSelectedFiles);
    }
  }, [externalSelectedFiles]);

  // Load saved path from localStorage on mount and load directory contents
  useEffect(() => {
    const savedPath = localStorage.getItem('currentPath');
    const pathToLoad = savedPath || '~';
    setCurrentPath(pathToLoad);
    
    // Only try to load directory if server is connected
    if (serverStatus === 'connected') {
      loadDirectory(pathToLoad);
    }
  }, [serverStatus]);

  // Save current path to localStorage whenever it changes
  useEffect(() => {
    if (currentPath && currentPath !== '~') {
      localStorage.setItem('currentPath', currentPath);
    }
  }, [currentPath]);

  // Load directory contents when path changes (but not on initial load)
  useEffect(() => {
    // Skip the initial load since it's handled in the mount effect
    if (directoryContents !== null && serverStatus === 'connected') {
      loadDirectory(currentPath);
    }
  }, [currentPath, serverStatus]);

  const loadDirectory = async (path) => {
    setIsLoading(true);
    setError('');
    
    try {
      const response = await fetch(`${serverUrl}/api/browse?path=${encodeURIComponent(path)}`);
      
      if (!response.ok) {
        throw new Error(`HTTP error! status: ${response.status}`);
      }
      
      const data = await response.json();
      
      if (data.error) {
        throw new Error(data.error);
      }
      
      setDirectoryContents(data);
    } catch (error) {
      console.error('Error loading directory:', error);
      setError(error.message);
      // Reset to root directory when browse fails
      setCurrentPath('/');
      // Try to load the root directory
      try {
        const rootResponse = await fetch(`${serverUrl}/api/browse?path=/`);
        if (rootResponse.ok) {
          const rootData = await rootResponse.json();
          if (!rootData.error) {
            setDirectoryContents(rootData);
            setError(''); // Clear error if root loads successfully
          }
        }
      } catch (rootError) {
        console.error('Error loading root directory:', rootError);
        // Keep the error message if root also fails
      }
    } finally {
      setIsLoading(false);
    }
  };

  const navigateToDirectory = (path) => {
    setCurrentPath(path);
  };

  const navigateToParent = () => {
    if (directoryContents && directoryContents.parent) {
      setCurrentPath(directoryContents.parent);
    }
  };

  const toggleFileSelection = (filePath) => {
    const newSelectedFiles = selectedFiles.includes(filePath)
      ? selectedFiles.filter(f => f !== filePath)
      : [...selectedFiles, filePath];
    
    setSelectedFiles(newSelectedFiles);
    
    if (onFilesSelected) {
      onFilesSelected(newSelectedFiles);
    }
  };

  const clearAllFiles = () => {
    setSelectedFiles([]);
    if (onFilesSelected) {
      onFilesSelected([]);
    }
  };

  const formatFileSize = (bytes) => {
    if (bytes === 0) return '0 B';
    const k = 1024;
    const sizes = ['B', 'KB', 'MB', 'GB'];
    const i = Math.floor(Math.log(bytes) / Math.log(k));
    return parseFloat((bytes / Math.pow(k, i)).toFixed(1)) + ' ' + sizes[i];
  };

  const formatDate = (date) => {
    return new Date(date).toLocaleDateString() + ' ' + new Date(date).toLocaleTimeString();
  };

  return (
    <div style={{
      padding: '20px',
      backgroundColor: 'rgba(255, 255, 255, 0.1)',
      borderRadius: '8px',
      margin: '10px',
      border: '1px solid rgba(255, 255, 255, 0.2)',
      display: 'flex',
      flexDirection: 'column'
    }}>
      <h3 style={{ margin: '0 0 15px 0', color: '#fff' }}>File System Browser</h3>
      
      {/* Server Status Bar - Only show when connected */}
      {serverStatus === 'connected' && (
        <div style={{ 
          marginBottom: '15px',
          padding: '10px',
          backgroundColor: 'rgba(40, 167, 69, 0.1)',
          border: '1px solid rgba(40, 167, 69, 0.3)',
          borderRadius: '4px',
          fontSize: '12px',
          flexShrink: 0
        }}>
          <div style={{ 
            display: 'flex', 
            alignItems: 'center', 
            justifyContent: 'space-between'
          }}>
            <div style={{ display: 'flex', alignItems: 'center' }}>
              <div style={{
                width: '8px',
                height: '8px',
                borderRadius: '50%',
                backgroundColor: '#28a745',
                marginRight: '8px'
              }} />
              <span style={{ 
                color: '#28a745',
                fontWeight: 'bold',
                marginRight: '10px'
              }}>
                Connected
              </span>
              <span style={{ color: '#ccc' }}>
                Server: {serverUrl}
              </span>
            </div>
            <button
              onClick={onOpenServerSettings}
              style={{
                padding: '4px 8px',
                backgroundColor: '#007bff',
                color: 'white',
                border: 'none',
                borderRadius: '3px',
                cursor: 'pointer',
                fontSize: '11px'
              }}
            >
              Configure
            </button>
          </div>
        </div>
      )}
      
      {/* Breadcrumb Navigation */}
      <div style={{ 
        marginBottom: '15px',
        padding: '10px',
        backgroundColor: 'rgba(255, 255, 255, 0.05)',
        borderRadius: '4px',
        fontSize: '14px',
        flexShrink: 0
      }}>
        <div style={{ color: '#fff', marginBottom: '5px' }}>Current Path:</div>
        <div style={{ color: '#ccc', wordBreak: 'break-all' }}>{currentPath}</div>
        {directoryContents && directoryContents.parent && (
          <button
            onClick={navigateToParent}
            style={{
              marginTop: '8px',
              padding: '4px 8px',
              backgroundColor: '#6c757d',
              color: 'white',
              border: 'none',
              borderRadius: '3px',
              cursor: 'pointer',
              fontSize: '12px'
            }}
          >
             Go to Parent Directory
          </button>
        )}
      </div>

      {/* Main Content Area - Side by Side */}
      <div style={{ 
        display: 'flex', 
        gap: '20px', 
        minHeight: '400px'
      }}>
        {/* Left Side - File Explorer */}
        <div style={{ flex: 1, display: 'flex', flexDirection: 'column', minWidth: 0, maxHeight: '60vh' }}>
          <div style={{ color: '#fff', marginBottom: '10px', fontSize: '14px', fontWeight: 'bold', flexShrink: 0 }}>
            File Explorer
          </div>
          
          {isLoading && (
            <div style={{ textAlign: 'center', padding: '20px', color: '#ccc', flex: 1, display: 'flex', alignItems: 'center', justifyContent: 'center' }}>
              Loading directory contents...
            </div>
          )}

          {serverStatus !== 'connected' && !isLoading && (
            <div style={{ 
              flex: 1,
              border: '1px solid rgba(255, 255, 255, 0.2)',
              borderRadius: '4px',
              display: 'flex',
              flexDirection: 'column',
              alignItems: 'center',
              justifyContent: 'center',
              padding: '40px 20px',
              minHeight: '300px'
            }}>
              <div style={{ 
                fontSize: '48px', 
                marginBottom: '20px',
                color: serverStatus === 'disconnected' ? '#dc3545' : '#ffc107'
              }}>
                
              </div>
              
              <div style={{ 
                color: '#fff', 
                fontSize: '18px', 
                marginBottom: '10px',
                textAlign: 'center'
              }}>
                Server Not Connected
              </div>
              
              <div style={{ 
                color: '#ccc', 
                fontSize: '14px', 
                marginBottom: '30px',
                textAlign: 'center',
                maxWidth: '400px'
              }}>
                {serverStatus === 'disconnected' 
                  ? 'The CodeMaps server is not running or not accessible. Please start the server and configure the connection.'
                  : 'Unable to connect to the server. Please check your configuration.'
                }
              </div>
              <br/>
              <div style={{ color: '#ccc', fontSize: '12px', lineHeight: '1.4' }}>
                See guide at:
                <br/>
                <a href="https://github.com/silverveskilt/code-maps-public" target="_blank" rel="noopener noreferrer" style={{ color: '#fff', fontSize: '12px', textDecoration: 'underline' }}>
                  https://github.com/silverveskilt/code-maps-public
                </a>
              </div>
              <br/>
              
              <button
                onClick={onOpenServerSettings}
                style={{
                  padding: '12px 24px',
                  backgroundColor: '#007bff',
                  color: 'white',
                  border: 'none',
                  borderRadius: '4px',
                  cursor: 'pointer',
                  fontSize: '14px',
                  fontWeight: 'bold'
                }}
              >
                Configure Server
              </button>
            </div>
          )}

          {error && serverStatus === 'connected' && (
            <div style={{ 
              padding: '10px', 
              backgroundColor: 'rgba(255, 0, 0, 0.1)', 
              color: '#ff6b6b',
              borderRadius: '4px',
              marginBottom: '15px',
              flexShrink: 0
            }}>
              Error: {error}
            </div>
          )}

          {directoryContents && !isLoading && (
            <div style={{ 
              flex: 1,
              border: '1px solid rgba(255, 255, 255, 0.2)',
              borderRadius: '4px',
              overflow: 'hidden',
              display: 'flex',
              flexDirection: 'column',
              minHeight: '300px'
            }}>
              <div style={{ 
                padding: '8px 12px',
                backgroundColor: 'rgba(255, 255, 255, 0.05)',
                borderBottom: '1px solid rgba(255, 255, 255, 0.2)',
                fontSize: '12px',
                color: '#ccc',
                flexShrink: 0
              }}>
                {directoryContents.items.length} items
              </div>
              
              <div style={{ 
                flex: 1,
                overflowY: 'auto',
                minHeight: '200px'
              }}>
                {directoryContents.items
                  .filter(item => !(item.isDirectory && item.name.startsWith('.')))
                  .map((item, index) => (
                  <div
                    key={index}
                    style={{
                      display: 'flex',
                      alignItems: 'center',
                      padding: '8px 12px',
                      borderBottom: '1px solid rgba(255, 255, 255, 0.1)',
                      cursor: item.isDirectory ? 'pointer' : 'default',
                      backgroundColor: item.isDirectory ? 'rgba(255, 255, 255, 0.05)' : 'transparent',
                      fontSize: '13px'
                    }}
                    onClick={() => item.isDirectory && navigateToDirectory(item.path)}
                  >
                    <div style={{ 
                      marginRight: '10px', 
                      fontSize: '16px',
                      color: item.isDirectory ? '#4dabf7' : (item.isCSharp ? '#51cf66' : '#adb5bd')
                    }}>
                      {item.isDirectory ? '' : (item.isCSharp ? '' : '')}
                    </div>
                    
                    <div style={{ flex: 1, color: '#fff', minWidth: 0 }}>
                      <div style={{ 
                        fontWeight: item.isCSharp ? 'bold' : 'normal',
                        whiteSpace: 'nowrap',
                        overflow: 'hidden',
                        textOverflow: 'ellipsis'
                      }}>
                        {item.name}
                      </div>
                      <div style={{ fontSize: '11px', color: '#ccc' }}>
                        {item.isFile ? `${formatFileSize(item.size)}  ${formatDate(item.modified)}` : 'Directory'}
                      </div>
                    </div>
                    
                    {item.isCSharp && (
                      <button
                        onClick={(e) => {
                          e.stopPropagation();
                          toggleFileSelection(item.path);
                        }}
                        style={{
                          padding: '4px 8px',
                          backgroundColor: selectedFiles.includes(item.path) ? '#dc3545' : '#28a745',
                          color: 'white',
                          border: 'none',
                          borderRadius: '3px',
                          cursor: 'pointer',
                          fontSize: '11px',
                          flexShrink: 0
                        }}
                      >
                        {selectedFiles.includes(item.path) ? 'Remove' : 'Add'}
                      </button>
                    )}
                  </div>
                ))}
              </div>
            </div>
          )}
        </div>

        {/* Right Side - Selected Files */}
        <div style={{ 
          width: '220px', 
          display: 'flex', 
          flexDirection: 'column',
          flexShrink: 0
        }}>
          <div style={{ color: '#fff', marginBottom: '10px', fontSize: '14px', fontWeight: 'bold', flexShrink: 0 }}>
            Selected Files ({selectedFiles.length})
          </div>
          
          {selectedFiles.length > 0 ? (
            <div style={{ 
              flex: 1,
              border: '1px solid rgba(255, 255, 255, 0.2)',
              borderRadius: '4px',
              overflow: 'hidden',
              display: 'flex',
              flexDirection: 'column',
              minHeight: '300px'
            }}>
              <div style={{ 
                padding: '8px 12px',
                backgroundColor: 'rgba(255, 255, 255, 0.05)',
                borderBottom: '1px solid rgba(255, 255, 255, 0.2)',
                fontSize: '12px',
                color: '#ccc',
                flexShrink: 0
              }}>
                {selectedFiles.length} file(s) selected
              </div>
              
              <div style={{ flex: 1, overflowY: 'auto', minHeight: '200px' }}>
                {selectedFiles.map((filePath, index) => (
                  <div
                    key={index}
                    style={{
                      display: 'flex',
                      justifyContent: 'space-between',
                      alignItems: 'center',
                      padding: '8px 12px',
                      borderBottom: '1px solid rgba(255, 255, 255, 0.1)',
                      fontSize: '12px'
                    }}
                  >
                    <span style={{ 
                      color: '#fff', 
                      flex: 1, 
                      wordBreak: 'break-all',
                      fontSize: '11px'
                    }}>
                      {filePath}
                    </span>
                    <button
                      onClick={() => toggleFileSelection(filePath)}
                      style={{
                        marginLeft: '8px',
                        padding: '2px 6px',
                        backgroundColor: '#dc3545',
                        color: 'white',
                        border: 'none',
                        borderRadius: '3px',
                        cursor: 'pointer',
                        fontSize: '10px',
                        flexShrink: 0
                      }}
                    >
                      
                    </button>
                  </div>
                ))}
              </div>
            </div>
          ) : (
            <div style={{ 
              flex: 1,
              border: '1px solid rgba(255, 255, 255, 0.2)',
              borderRadius: '4px',
              display: 'flex',
              alignItems: 'center',
              justifyContent: 'center',
              color: '#ccc',
              fontSize: '14px',
              fontStyle: 'italic',
              textAlign: 'center',
              padding: '20px',
              minHeight: '300px'
            }}>
              No files selected.<br/>
              Navigate and click "Add" next to supported files (.cs)
            </div>
          )}
          
          {selectedFiles.length > 0 && (
            <button
              onClick={clearAllFiles}
              style={{
                marginTop: '10px',
                padding: '8px 16px',
                backgroundColor: '#dc3545',
                color: 'white',
                border: 'none',
                borderRadius: '4px',
                cursor: 'pointer',
                fontSize: '12px',
                flexShrink: 0
              }}
            >
              Clear All Files
            </button>
          )}
          
          {selectedFiles.length > 0 && (
            <button
              onClick={onGenerateGraph}
              disabled={serverStatus !== 'connected'}
              style={{
                marginTop: '10px',
                padding: '8px 16px',
                backgroundColor: serverStatus === 'connected' ? '#28a745' : '#6c757d',
                color: 'white',
                border: 'none',
                borderRadius: '4px',
                cursor: serverStatus === 'connected' ? 'pointer' : 'not-allowed',
                fontSize: '12px',
                flexShrink: 0,
                opacity: serverStatus === 'connected' ? 1 : 0.6
              }}
              title={serverStatus !== 'connected' ? 'Server not connected' : 'Generate graph from selected files'}
            >
              {serverStatus === 'connected' ? 'View Graph' : 'View Graph (No access)'}
            </button>
          )}
        </div>
      </div>
    </div>
  );
};

export default FileSystemBrowser; 