import React, { useState } from 'react';
import { useServerConfig } from '../contexts/ServerConfigContext';

const ServerSettings = ({ isOpen, onClose }) => {
  const { serverUrl, serverStatus, isChecking, updateServerUrl, checkServerConnection } = useServerConfig();
  const [tempUrl, setTempUrl] = useState(serverUrl);
  const [testStatus, setTestStatus] = useState(null);

  const handleSave = () => {
    updateServerUrl(tempUrl);
    onClose();
  };

  const handleCancel = () => {
    setTempUrl(serverUrl);
    setTestStatus(null);
    onClose();
  };

  const handleTestConnection = () => {
    setTestStatus('checking');
    checkServerConnection(tempUrl, (status) => {
      setTestStatus(status);
    });
  };

  const getStatusColor = () => {
    const status = testStatus || serverStatus;
    switch (status) {
      case 'connected': return '#28a745';
      case 'disconnected': return '#dc3545';
      default: return '#6c757d';
    }
  };

  const getStatusText = () => {
    const status = testStatus || serverStatus;
    switch (status) {
      case 'connected': return 'Connected';
      case 'disconnected': return 'Disconnected';
      case 'checking': return 'Checking...';
      default: return 'Unknown';
    }
  };

  if (!isOpen) return null;

  return (
    <div style={{
      position: 'fixed',
      top: 0,
      left: 0,
      right: 0,
      bottom: 0,
      backgroundColor: 'rgba(0, 0, 0, 0.5)',
      display: 'flex',
      justifyContent: 'center',
      alignItems: 'center',
      zIndex: 1000
    }}>
      <div style={{
        backgroundColor: '#2a2a2a',
        borderRadius: '8px',
        padding: '24px',
        minWidth: '400px',
        border: '1px solid rgba(255, 255, 255, 0.2)'
      }}>
        <h3 style={{ color: '#fff', margin: '0 0 20px 0' }}>Server Configuration</h3>
        
        <div style={{ marginBottom: '20px' }}>
          <label style={{ 
            display: 'block', 
            color: '#fff', 
            marginBottom: '8px',
            fontSize: '14px'
          }}>
            Server URL:
          </label>
          <input
            type="text"
            value={tempUrl}
            onChange={(e) => setTempUrl(e.target.value)}
            placeholder="http://localhost:3001"
            style={{
              width: '100%',
              padding: '10px 12px',
              borderRadius: '4px',
              border: '1px solid rgba(255, 255, 255, 0.3)',
              backgroundColor: 'rgba(255, 255, 255, 0.1)',
              color: '#fff',
              fontSize: '14px',
              boxSizing: 'border-box'
            }}
          />
        </div>

        <div style={{ marginBottom: '20px' }}>
          <div style={{ 
            display: 'flex', 
            alignItems: 'center', 
            marginBottom: '8px'
          }}>
            <span style={{ color: '#fff', fontSize: '14px', marginRight: '10px' }}>
              Status:
            </span>
            <div style={{
              width: '8px',
              height: '8px',
              borderRadius: '50%',
              backgroundColor: getStatusColor(),
              marginRight: '8px'
            }} />
            <span style={{ 
              color: getStatusColor(), 
              fontSize: '14px',
              fontWeight: 'bold'
            }}>
              {getStatusText()}
            </span>
          </div>
          
          <button
            onClick={handleTestConnection}
            disabled={isChecking}
            style={{
              padding: '6px 12px',
              backgroundColor: '#6c757d',
              color: 'white',
              border: 'none',
              borderRadius: '4px',
              cursor: isChecking ? 'not-allowed' : 'pointer',
              fontSize: '12px',
              opacity: isChecking ? 0.6 : 1
            }}
          >
            {isChecking ? 'Checking...' : 'Test Connection'}
          </button>
        </div>

        <div style={{ 
          padding: '12px', 
          backgroundColor: 'rgba(255, 255, 255, 0.05)', 
          borderRadius: '4px',
          marginBottom: '20px'
        }}>
          <div style={{ color: '#ccc', fontSize: '12px', marginBottom: '8px' }}>
            <strong>Quick Setup:</strong>
          </div>
          <div style={{ color: '#ccc', fontSize: '12px', lineHeight: '1.4' }}>
            1. Download and run the CodeMaps server<br/>
            2. Point the browser towards your local server<br/>
            3. Test connection to start analyzing your code
          </div>
        </div>

        <div style={{ 
          display: 'flex', 
          gap: '10px', 
          justifyContent: 'flex-end' 
        }}>
          <button
            onClick={handleCancel}
            style={{
              padding: '8px 16px',
              backgroundColor: '#6c757d',
              color: 'white',
              border: 'none',
              borderRadius: '4px',
              cursor: 'pointer',
              fontSize: '14px'
            }}
          >
            Cancel
          </button>
          <button
            onClick={handleSave}
            style={{
              padding: '8px 16px',
              backgroundColor: '#28a745',
              color: 'white',
              border: 'none',
              borderRadius: '4px',
              cursor: 'pointer',
              fontSize: '14px'
            }}
          >
            Save
          </button>
        </div>
      </div>
    </div>
  );
};

export default ServerSettings; 