import React, { memo } from 'react';

export default memo(({ data, children }) => {
  return (
    <div>
      {/* Class name label positioned at top left corner */}
      <div style={{
        position: 'absolute',
        top: '-23px',
        left: '2px',
        backgroundColor: data.classColor?.light,
        color: data.classColor?.contrastText,
        padding: '4px 8px',
        borderRadius: '4px',
        borderBottomLeftRadius: '0px',
        borderBottomRightRadius: '0px',
        fontSize: '12px',
        fontWeight: 'bold',
        zIndex: 10,
      }}>
        {data.label}
      </div>
      {/* Group content */}
      {children}
    </div>
  );
}); 